main {
  text-align: center;
  height: 83.2vh;
  background-color: #2c3e50;
}

.array-container {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
}

.arr-el {
  width: 0.2rem;
  background-color: #e87e04;
  display: inline-block;
  margin: 0 0.2rem;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}

